import Vue from 'vue'
import VueRouter from 'vue-router'
import Main from '../components/Main.vue'
import Mainphone from '../components/Mainphone.vue'
import Datastructure from '../components/datastructure.vue'
import Python from '../components/python.vue'

Vue.use(VueRouter)

const routes = [
  // 重定向
  {
    path: '/',
    meta: {
      title: '仇志金 Zhijin Qiu 齐鲁工业大学 山东省科学院海洋仪器仪表研究所'
    },
    redirect: '/main'
  },
  {
    path: '/main',
    meta: {
      title: '仇志金 Zhijin Qiu 齐鲁工业大学 山东省科学院海洋仪器仪表研究所'
    },
    component: Main
  },
  {
    path: '/main_phone',
    meta: {
      title: '仇志金 Zhijin Qiu 齐鲁工业大学 山东省科学院海洋仪器仪表研究所'
    },
    component: Mainphone
  },
  {
    path: '/datastructure',
    meta: {
      title: '数据结构 仇志金 Zhijin Qiu 齐鲁工业大学 山东省科学院海洋仪器仪表研究所'
    },
    component: Datastructure
  },
  {
    path: '/python',
    meta: {
      title: 'Python 仇志金 Zhijin Qiu 齐鲁工业大学 山东省科学院海洋仪器仪表研究所'
    },
    component: Python
  },
  {
    path: '/news',
    redirect: '/'
  },
  {
    path: '/Projects',
    redirect: '/'
  },
  {
    path: '/Journal',
    redirect: '/'
  },
  {
    path: '/Conference',
    redirect: '/'
  },
  {
    path: '/Patents',
    redirect: '/'
  },
  {
    path: '/Teaching',
    redirect: '/'
  }
]

const router = new VueRouter({
  routes
})

const originalReplace = VueRouter.prototype.replace
VueRouter.prototype.replace = function replace (location) {
  return originalReplace.call(this, location).catch(err => err)
}

